import React, { Component } from "react";
import { Tabs, Modal, Button, Form, Input, message } from "antd";
import "./index.css";
import SlideVerify from "slide-verify";
import { decrypt } from "../../utils/crypto";
import $axios from "@/axios/$axios";
const { TabPane } = Tabs;

class Query extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dialogVisible: false,
      captchaVisible: false,
      allowQuery: "forbid",
      orderInfo: null,
      formData: null,
      loading: false,
    };
  }
  handleCheck = async () => {
    this.setState({ loading: true });
    if (this.state.formData.orderId !== undefined) {
      $axios(`/order/query?orderId=${this.state.formData.orderId}`)
        .then((result) => {
          this.setState({ orderInfo: result.data });
          this.setState({ loading: false });
          this.showModal();
        })
        .catch((err) => {
          console.log(err);
          this.setState({ loading: false });
          message.warning("");
        });
    } else {
      $axios(
        `/order/query?email=${this.state.formData.email}&&password=${this.state.formData.password}`
      )
        .then((result) => {
          this.setState({ orderInfo: result.data });
          this.showModal();
          this.setState({ loading: false });
        })
        .catch((err) => {
          message.warning("");
          this.setState({ loading: false });
        });
    }
  };
  showModal = () => {
    if (localStorage.getItem("orderInfo")) {
      this.setState({
        orderInfo: JSON.parse(decrypt(localStorage.getItem("orderInfo"))),
      });
    } else {
      message.warning("");
      return;
    }

    this.setState({
      dialogVisible: true,
    });
    this.setState({
      allowQuery: "forbid",
    });
  };

  handleOk = (e) => {
    this.setState({
      dialogVisible: false,
    });
    this.setState({ orderInfo: null });
  };

  handleCancel = (e) => {
    console.log(e);
    this.setState({
      dialogVisible: false,
    });
    this.setState({ orderInfo: null });
  };
  handleVerify = () => {
    this.setState({ captchaVisible: true });
    let Slide = new SlideVerify({
      elementId: "captcha", // DOM
      onSuccess: () => {
        console.log("success");
        this.setState({ captchaVisible: false });
        this.setState({ allowQuery: "allow" });
      }, // 
      onFail: () => {
        console.log("fail");
      }, // 
      onRefresh: () => {
        console.log("refresh");
      }, // 
      photo: "https://picsum.photos/310/210", // 
    });
  };
  onFinish = (values) => {
    console.log(values);
    this.setState({ formData: values });
    this.handleCheck();
  };
  render() {
    return (
      <div className="query-container">
        {this.state.dialogVisible ? (
          <Modal
            title=""
            visible={this.state.dialogVisible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            style={{ userSelect: "text" }}
            footer={[
              <Button key="confirm" type="primary" onClick={this.handleOk}>
                
              </Button>,
            ]}
          >
            <p>{this.state.orderInfo.orderId}</p>
            <p>{this.state.orderInfo.date}</p>
            <p>
              {this.state.orderInfo.productName}
              {this.state.orderInfo.levelName}
            </p>
            <p>{this.state.orderInfo.price}</p>
            <p>{this.state.orderInfo.code}</p>
          </Modal>
        ) : null}

        <div
          id="captcha"
          className="query-captcha"
          style={this.state.captchaVisible ? {} : { display: "none" }}
        ></div>
        <p className="query-alert"></p>
        <Tabs defaultActiveKey="1" className="query-box-container">
          <TabPane tab="" key="1" className="query-by-local">
            <div>
              <Button
                onClick={() => {
                  this.handleVerify();
                }}
                size="medium"
                type={this.state.allowQuery === "forbid" ? "primary" : ""}
              >
                
              </Button>
            </div>

            <Button
              type="primary"
              onClick={this.showModal}
              size="medium"
              disabled={this.state.allowQuery === "forbid"}
              loading={this.state.loading}
            >
              
            </Button>

            <div></div>
          </TabPane>
          <TabPane tab="" key="2">
            <Form onFinish={this.onFinish} onFinishFailed={this.onFinishFailed}>
              <Form.Item
                // label=""
                name="orderId"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
              >
                <Input
                  placeholder=""
                  className="query-input-box"
                  style={{ borderRadius: "5px" }}
                />
              </Form.Item>
              <Button
                onClick={() => {
                  this.handleVerify();
                }}
                size="medium"
                type={this.state.allowQuery === "forbid" ? "primary" : ""}
              >
                
              </Button>

              <Form.Item>
                <Button
                  type="primary"
                  htmlType="submit"
                  disabled={this.state.allowQuery === "forbid"}
                  size="medium"
                  style={{ marginTop: "10px" }}
                  loading={this.state.loading}
                >
                  
                </Button>
              </Form.Item>
            </Form>
          </TabPane>
          <TabPane tab="" key="3">
            <Form onFinish={this.onFinish} onFinishFailed={this.onFinishFailed}>
              <Form.Item
                // label=""
                name="email"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
              >
                <Input
                  placeholder=""
                  className="query-input-mail"
                  style={{ borderRadius: "5px" }}
                />
              </Form.Item>
              <Form.Item
                name="password"
                // label=""
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
              >
                <Input
                  placeholder=""
                  className="query-input-mail"
                  style={{ borderRadius: "5px" }}
                />
              </Form.Item>
              <Button
                onClick={() => {
                  this.handleVerify();
                }}
                size="medium"
                type={this.state.allowQuery === "forbid" ? "primary" : ""}
              >
                
              </Button>
              <Form.Item>
                <Button
                  className=""
                  type="primary"
                  htmlType="submit"
                  size="medium"
                  disabled={this.state.allowQuery === "forbid"}
                  style={{ marginTop: "10px" }}
                  loading={this.state.loading}
                >
                  
                </Button>
              </Form.Item>
            </Form>
          </TabPane>
        </Tabs>
      </div>
    );
  }
}

export default Query;
