//
import React, { Component } from "react";
import { Form, Input, Button, Row, message } from "antd";
import { AlipayCircleOutlined } from "@ant-design/icons";
import "./index.css";
import { connect } from "react-redux";
import { handleFetchForm, handleVerifyDialog } from "@/redux/actions/form";
import $axios from "@/axios/$axios";
import VerifyId from "../../components/verifyId";
class Alipay extends Component {
  constructor(props) {
    super(props);
    this.state = { loading: false };
  }
  formRef = React.createRef();
  handleVerify = () => {
    if (this.props.isVerified) {
      console.log(this.formRef);
      this.formRef.current.setFieldsValue(this.props.formData);
    } else {
      this.props.handleVerifyDialog(true);
    }
  };
  onFinish = (values) => {
    this.setState({ loading: true });
    if (!this.props.isVerified) {
      this.props.handleVerifyDialog(true);
      this.setState({
        loading: false,
      });
      return;
    }
    $axios
      .post(`/alipay/${this.props.formData._id}`, {
        ...values,
        paymentName: "",
      })
      .then(() => {
        message.success("");
        this.setState({ loading: false });
        this.props.handleFetchForm();
      })
      .catch(() => {
        this.setState({ loading: false });
        message.error("");
      });
  };
  render() {
    const formItemLayoutWithOutLabel = {
      wrapperCol: {
        xs: { span: 24, offset: 2 },
        sm: { span: 16, offset: 4 },
      },
    };
    const formItemLayout = {
      labelCol: {
        sm: { span: 3 },
      },
      wrapperCol: {
        sm: { span: 10, offset: 0 },
      },
    };
    return (
      <div className="alipay-container" style={{ position: "relative" }}>
        <VerifyId />
        <Row justify="center" style={{ marginTop: "20px" }}>
          <AlipayCircleOutlined className="alipay-icon" />
          <p className="alipay-title"></p>
        </Row>
        <div className="alipay-page-message">
          <p className="alipay-message-title"> </p>
          <p>
            
            <br />
            <br />
            
            <br />
            <br />
            <a
              href="https://51.ruyo.net/12354.html"
              target="_blank"
              rel="noopener noreferrer"
            >
              
            </a>
          </p>
        </div>
        <Form
          {...formItemLayout}
          onFinish={this.onFinish}
          onFinishFailed={this.onFinishFailed}
          ref={this.formRef}
          name="control-ref"
          initialValues={
            this.props.isVerified && this.props.formData
              ? this.props.formData
              : null
          }
          style={{ marginTop: "40px" }}
        >
          <Form.Item label="" name="paymentName">
            <Input placeholder="" defaultValue="" />
          </Form.Item>
          <Form.Item
            label="ID"
            name="appId"
            rules={[
              {
                required: true,
                message: "ID",
              },
            ]}
          >
            <Input placeholder="ID" />
          </Form.Item>
          <Form.Item
            name="publicKey"
            label=""
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input.TextArea
              onChange={this.onDescChange}
              placeholder=""
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>
          <Form.Item
            name="secretKey"
            label=""
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input.TextArea
              onChange={this.onDescChange}
              placeholder="RSA2(SHA256)"
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>
          <Form.Item
            label=""
            name="notifyUrl"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input placeholder="httphttps" />
          </Form.Item>
          <Form.Item {...formItemLayoutWithOutLabel}>
            <Button onClick={this.handleVerify} style={{ marginRight: "10px" }}>
              
            </Button>
            <Button
              type="primary"
              htmlType="submit"
              loading={this.state.loading}
            >
              
            </Button>
          </Form.Item>
        </Form>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return { isVerified: state.form.isVerified };
};
const actionCreator = {
  handleFetchForm,
  handleVerifyDialog,
};
export default connect(mapStateToProps, actionCreator)(Alipay);
