import React, { Component } from "react";
import { Form, Input, Button, Row, Select, message } from "antd";
import { createFromIconfontCN } from "@ant-design/icons";
import "./index.css";
import $axios from "@/axios/$axios";
const IconFont = createFromIconfontCN({
  scriptUrl: "//at.alicdn.com/t/font_1693087_csjervhanqr.js",
});
class Paypal extends Component {
  constructor(props) {
    super(props);
    this.state = { loading: false };
  }
  onFinish = (values) => {
    this.setState({ loading: true });
    $axios
      .post(`/paypal/${this.props.formData._id}`, values)
      .then(() => {
        message.success("");
        this.setState({ loading: false });
      })
      .catch(() => {
        message.error("");
        this.setState({ loading: false });
      });
  };
  render() {
    const formItemLayoutWithOutLabel = {
      wrapperCol: {
        xs: { span: 24, offset: 2 },
        sm: { span: 16, offset: 7 },
      },
    };
    const formItemLayout = {
      labelCol: {
        sm: { span: 3 },
      },
      wrapperCol: {
        sm: { span: 10, offset: 0 },
      },
    };
    return (
      <div className="paypal-container" style={{ position: "relative" }}>
        <Row justify="center" style={{ marginTop: "20px" }}>
          <IconFont type="icon-social-paypal" className="paypal-icon" />
          <p className="paypal-title"> Paypal</p>
        </Row>
        <div
          className="alipay-page-message"
          style={{ borderLeft: "4px solid #113984" }}
        >
          <p className="alipay-message-title"> </p>
          <p>
            Paypal 
            <br />
            <br />
            Github star 
            <br />
            <br />
            <a href="https://" target="_blank" rel="noopener noreferrer">
              
            </a>
          </p>
        </div>
        <Form
          {...formItemLayout}
          onFinish={this.onFinish}
          onFinishFailed={this.onFinishFailed}
          // initialValues={this.props.formData ? this.props.formData : null}
          style={{ marginTop: "40px" }}
        >
          <Form.Item label="" name="paymentName">
            <Input placeholder="" defaultValue="Paypal" />
          </Form.Item>
          <Form.Item
            label="ClientID"
            name="clientID"
            rules={[
              {
                required: true,
                message: "ClientID",
              },
            ]}
          >
            <Input.TextArea
              onChange={this.onDescChange}
              placeholder="ClientID"
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>
          <Form.Item
            name="secretKey"
            label="Paypal "
            rules={[
              {
                required: true,
                message: " Paypal ",
              },
            ]}
          >
            <Input.TextArea
              onChange={this.onDescChange}
              placeholder=" Paypal "
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>
          <Form.Item
            label=""
            name="exchangeRate"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item
            name="mode"
            label=""
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Select placeholder="">
              <Select.Option value=""></Select.Option>
              <Select.Option value=""></Select.Option>
            </Select>
          </Form.Item>
          <Form.Item
            label=""
            name="answer1"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            label=""
            name="answer2"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item {...formItemLayoutWithOutLabel}>
            <Button
              type="primary"
              htmlType="submit"
              disabled
              loading={this.state.loading}
            >
              
            </Button>
          </Form.Item>
        </Form>
      </div>
    );
  }
}

export default Paypal;
