import React, { Component } from "react";
import { Descriptions, Row, Button } from "antd";
import { parseFormData } from "../../../utils/productUtil";
class AddStepTwo extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { formData } = this.props;
    const renderContact = (contact) => {
      return contact.split("\n").map((item, index) => {
        return <div key={index}>{item}</div>;
      });
    };
    const renderLevelTable = (levels, data) => {
      const {
        levelName,
        levelPrice,
        levelDesc,
        levelLimit,
        levelNote,
      } = parseFormData(data, null);
      let arr = [];
      for (let i = 1; i <= levels; i++) {
        arr.push(i);
      }
      const renderLevelDesc = (descArr) => {
        return descArr.map((item, index) => {
          return <div key={index}>{item}</div>;
        });
      };
      return arr.map((item) => {
        return (
          <Descriptions.Item label={`${item}`} span={3} key={item}>
            {item}: {levelName[item - 1]}
            <br />
            {item}:{" "}
            {`${levelPrice[item - 1].price}/${levelPrice[item - 1].unit}`}
            <br />
            {item}:
            <br />
            {renderLevelDesc(levelDesc[item - 1])}
            <br />
            {item}: {levelLimit[item - 1]}
            <br />
            {item}: {levelNote[item - 1]}
            <br />
          </Descriptions.Item>
        );
      });
    };
    return (
      <div className="add-step-two" style={{ padding: "50px 150px" }}>
        <Descriptions
          bordered
          column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
        >
          <Descriptions.Item label="">
            {formData.productName}
          </Descriptions.Item>
          <Descriptions.Item label="" span={2}>
            {formData.productInfo}
          </Descriptions.Item>
          {renderLevelTable(formData.memberLevel, formData)}
          {formData.productType === 1 ? null : (
            <Descriptions.Item label="" span={4}>
              {formData.callbackUrl}
            </Descriptions.Item>
          )}

          <Descriptions.Item label=""></Descriptions.Item>
          <Descriptions.Item label={``}>
            {renderContact(formData.contact)}
          </Descriptions.Item>
        </Descriptions>
        <Row justify="center" style={{ marginTop: "40px" }}>
          <Button
            onClick={this.props.handlePrev}
            style={{ marginRight: "30px" }}
          >
            
          </Button>
          <Button type="primary" onClick={this.props.handleNext}>
            
          </Button>
        </Row>
      </div>
    );
  }
}

export default AddStepTwo;
